import React from 'react'

import './ProfileBottom.scss';

export const ProfileBottom = ({ image, id, name, price, rarity, status, setOpenSaleMenu, setItemId, setItemPrice }) => {
  const handleSaleItem = (id) => {
    if (id) {
      setItemId(id);
      setItemPrice(price)
      setOpenSaleMenu(true);
    }
  };

  return (
    <>
      <div className={`ProfileBottomCard ${
        rarity === '' ? 'purple' :
        rarity === ' ' ? 'blue' :
        rarity === '' ? 'red' :
        rarity === '' ? 'green' :
        rarity === ' ' ? 'blue' :
        rarity === ' ' ? 'yellow': 
        rarity === '' ? 'pink' : ''}`}
      >
        <div className={`status ${status === '' ? 'sold' : ''}`}  onClick={status === '' ? null : () => handleSaleItem(id)}>{price}  {status !== '' && <span></span>}</div>
        <div className={`ProfileCardInner ${
          rarity === '' ? 'purple' :
          rarity === ' ' ? 'blue' :
          rarity === '' ? 'red' :
          rarity === '' ? 'green' :
          rarity === ' ' ? 'blue' :
          rarity === ' ' ? 'yellow': 
          rarity === '' ? 'pink' : ''}`}
        >
          <div className="ProfileBottomBlockRing" style={{ border: `2px solid ${
              rarity === '' ? '#9830b3' :
              rarity === ' ' ? '#3092bb' :
              rarity === '' ? '#db4343' :
              rarity === '' ? '#1dd87e' :
              rarity === ' ' ? '#3092bb' :
              rarity === ' ' ? '#cbde22': 
              rarity === '' ? '#c9405d' : ''}`}}>
            <img src={image} alt="" />
          </div>
          <div className='ProfileBottomBlockDescription'>
            <p className='name'>{name}</p>
            <span className='categoryName' 
            style={{ color: `${
              rarity === '' ? '#7257a8' :
              rarity === ' ' ? '#33687e' :
              rarity === '' ? '#7f3535' :
              rarity === '' ? '#307d58' :
              rarity === ' ' ? '#33687e' :
              rarity === ' ' ? '#7c842d': 
              rarity === '' ? '#7f357b' : ''}`}}
            >{rarity}</span>
          </div>
        </div>
      </div>
    </>
  )
}
